import React, { Component } from 'react';
import { Card, Button } from 'semantic-ui-react';
import Layout from '../components/Layout';
import record from '../ethereum/record';
import web3 from '../ethereum/web3';

class RecordsList extends Component {
    static async getInitialProps() {
        const allRecords = await record.methods.getPatients().call();

        return { allRecords };
    }

    renderRecords() {
        const items = this.props.records.map(address => {
            return {
                header: address,
                description: (
                    <a>View Campaign</a>
                ),
                fluid: true
            };
        });
        //Add all records to card group
        return <Card.Group items={items} />;
    }

    render() {
        return (
            <Layout>
                <div>
                    <h3>Open Campaigns</h3>
                    <Link route='/campaigns/new'><a><Button content="Create Campaign" icon="add circle" primary floated="right"/></a></Link>
                    {this.renderRecords()}
                </div>
            </Layout>
        );
    }
}

export default RecordsList;